//variavel que √© usada como uma semente para a gera√ß√£o de n√∫meros aleatorios
let _seed;
//viriavel boolean inicializada como verdadeiro
let validator = true;
//objeto vazio usado para armazenar o resultado da valida√ß√£o
let payload = {};
//array de strings que contem n√∫meros
let numbers = [
  "0.3156106622882362",
  "0.5513213466665547",
  "0.3301843000590452",
  "0.04769233684601204",
  "0.4450511749319048",
  "0.8014283726497524",
  "0.6599671903586568",
  "0.39207733233979525",
  "0.9243979984480575"
];

//recebe uma seed (-seed(192837465)-) como argumento e atribui √° variavel 
export function seed(seed) {
  _seed = seed;
}

//fun√ß√£o que gera um n√∫mero aleat√≥rio com a variavel _seed com um algoritmo 
export function random() {
  const x = Math.sin(_seed++) * 10000;
  return x - Math.floor(x);
}
//recebe um array como argumento e verifica se cada elemento do array est√° presente em "numbers"
export function checkRandomNumber(array) {
  //itera sobre cada elemento do array fornecido como argumento
  array.forEach((element) => {
    //verifica se o elemento atual convertido para string est√° presente no array "numbers"
    if (!numbers.includes(element.toString())) {
      //se o elemento n√£o est√° presente, define a vari√°vel "validator" como false
      validator = false;
    }
  });

  //verifica o valor da vari√°vel "validator"
  validator
    //se "validator" √© verdadeiro, configura o objeto "payload" com status "success"
    ? (payload = JSON.parse('{ "status": "success"}'))
    //se "validator" √© falso, configura o objeto "payload" com status "error"
    : (payload = JSON.parse('{ "status": "error"}'));

  //retorna o objeto "payload" ap√≥s a verifica√ß√£o
  return payload;
}
